import {isNil, isString} from './is';
/**
 * 
 * @function toArray
 * @param {any} arrayLike - 
 * @return {array} array
 */
export function toArray(arrayLike) {
    return [].slice.call(arrayLike);
}
/**
 * 
 * @function toNumber
 * @param {any} value - 
 * @param { any} [defaultValue=null] - 
 * @return {Number} number
 */
export function toNumber(value, defaultValue = null) {
    if (isNil(value)) {
        return defaultValue;
    }

    return Number(value);
}
/**
 * 
 * @function toBoolean
 * @param {any} value - 
 * @param { any} [defaultValue=null] - 
 * @return {Boolean} boolean
 */
export function toBoolean(value, defaultValue = false) {
    if (isNil(value)) {
        return defaultValue;
    }

    return Boolean(value);
}
/**
 * 
 * @function toString
 * @param {any} value - 
 * @param { any} [defaultValue=null] - 
 * @return {string} 
 */
export function toString(value, defaultValue = null) {
    if (isNil(value)) {
        return defaultValue;
    }

    if (isString(value)) {
        return value;
    }

    return String(value);
}
