import {isType} from './type';
import typeError from './type-error';
/**
 * 
 * @author wangyongqing01
 * @function dateFormat
 * @param {Date} d - date 
 * @param {string} [pattern = 'yyyy-MM-dd'] - 
 * @return {string} 
 * @example
 *	var d = new Date();
 *  dateFormat(d," yyyyMd\n yyyy-MM-dd\n MM-dd-yy\n yyyy-MM-dd hh:mm:ss")
 *  //20181110\n 2018-01-01\n 01-01-18\n 2018-11-12 12:01:02
 */
export default (d, pattern = 'yyyy-MM-dd') => {
    if (!isType(d, 'date')) {
        throw typeError('d', 'Date', d);
    }

    let y = d.getFullYear().toString();
    let o = {
        M: d.getMonth() + 1, // month
        d: d.getDate(), // day
        h: d.getHours(), // hour
        m: d.getMinutes(), // minute
        s: d.getSeconds() // second
    };
    pattern = pattern.replace(/(y+)/gi, (a, b) => y.substr(4 - Math.min(4, b.length)));
    Object.keys(o).forEach(i => {
        pattern = pattern.replace(new RegExp('(' + i + '+)', 'g'), (a, b) =>
            o[i] < 10 && b.length > 1 ? '0' + o[i] : o[i]
        );
    });
    return pattern;
};
