import UA from './ua';
/**
 * 
 * @function isBox
 * @param {''|lite|pro|info|mission} [subName=''] - 
 * @param {string} ua -  userAgent navigator.userAgent
 * @return { boolean} 
 * @example
 * isBoxApp()
 * isBoxApp('lite')
 * isBoxApp('pro', 'fdafafdafaf pro baiduboxapp')
 */
export default (subName = '', ua = UA) => {
    if (subName === 'main') {
        subName = '';
    }

    // baiduboxapp: UA
    // biduboxapp:  (le.com)UA
    const reg =  / (baiduboxapp|biduboxapp)\//i;
    if (reg.test(ua) === false) {
        // 
        return false;
    }

    let matchRes = ua.match(reg);
    let sign = matchRes[1];
    if (!~ua.indexOf(`${subName} ${sign}`)) {
        //  app  / lite / info / pro 
        return false;
    }

    return true;
};
