import {isString, isPlainObject, isArray, isNil, isFunction} from './is';
import type from './type';
import getGlobalFunc from './get-global-func';
import uriEncode from './strict-uri-encode';

/**
 * url JSON.stringify 
 * @function json2query
 * @param  {object} json - json
 * @param  {boolean} [encode = false] -  encodeURI /
 * @return {string}  str  - string
 * @example
 * json2query({
 *       a: 1,
 *       b: 'string'
 *  });
 * //'a=1&b=string'
 * json2query({
 *       a: 1,
 *       b: 'string',
 *       c: [1, 2, 3]
 *   });
 * //'a=1&b=string&c=[1,2,3]'
 * json2query({
 *       a: 1,
 *       b: 'string',
 *       c: [1, 2, 3]
 *   }, true);
 * //'a=1&b=string&c=%5B1%2C2%2C3%5D'
 */
export default function json2query(json, encode = false) {
    if (isString(json)) {
        return json;
    }

    const arr = [];
    for (let i in json) {
        let j = json[i];

        if (isArray(j)) {
            j = JSON.stringify(mapArray(j));
        } else if (isNil(j)) {
            j = '';
        } else if (isPlainObject(j)) {
            //  callback 
            j = JSON.stringify(j);
        } else if (isFunction(j)) {
            j = getGlobalFunc(j);
        }

        // string/number/boolean 
        arr.push(`${i}=${encode ? uriEncode(j) : j}`);
    }

    return arr.join('&');
}
function mapArray(args) {
    return args.map(arg => {
        switch (type(arg)) {
            case 'string':
                return arg;
            case 'function':
                return getGlobalFunc(arg);
            default:
                return arg;
        }
    });
}
