/**
 * @file  ls  ss  cache 
 * @author wangyongqing <wangyongqing01@baidu.com>
 */
let storageSupport = {};
['local', 'session'].forEach(v => {
    try {
        const name = v + 'Storage';
        let t = name in window && window[name] !== null;
        const test = {
            k: '__xbox_test_',
            v: 'it\'s great'
        };
        if (t) {
            const storage = window[name];
            storage.setItem(test.k, test.v);
            t = test.v === storage.getItem(test.k);
            storage.removeItem(test.k);
        }

        storageSupport[v] = true;
    }
    catch (e) {
        storageSupport[v] = false;
    }
});

const fakeStorage = {
    __fake__: true,
    data: {},
    setItem: (k, v) => {
        fakeStorage.data[k] = v;
    },
    getItem: k => {
        return fakeStorage.data[k];
    },
    removeItem: k => {
        delete fakeStorage.data[k];
    }
};
const defaultValue = undefined;

/**
 * localstoragesessionStorage
 * @class Storage
 * @param  {string} [prefix='_default_'] 
 * @param  {number} [expire=0] 
 * @param  {string} [type='local']   localsession
 * @author wangyongqing01
 */
export class Storage {
    constructor(prefix = '_default_', expire = 0, type = 'session') {
        if (type.length > 7) {
            // localstorage -> local
            // sessionstorage -> session
            type = type.slice(0, -7);
        }

        this.type = type;
        this.prefix = prefix + '_';
        this.storage = storageSupport[type] ? window[type + 'Storage'] : fakeStorage;
    }

    /**
     * key
     * @return {Storage} cache
     */
    clearExpire() {
        const keys = this.getKeys();
        const self = this;
        keys.forEach(key => {
            // getstorage
            self.get(key);
        });
        return this;
    }

    /**
     * keys
     * @return {Array} keys
     */
    getKeys() {
        let storage = this.storage;
        if (storage.__fake__) {
            storage = storage.data;
        }

        const reg = new RegExp('^' + this.prefix);
        const keys = Object.keys(storage);
        const result = [];
        for (let i = 0, len = keys.length; i < len; i++) {
            const key = keys[i];
            if (reg.test(key)) {
                result.push(key.replace(reg, ''));
            }

        }
        return result;
    }

    /**
     *  Object.entries 
     * @return {array} 
     */
    getEntries() {
        let storage = this.storage;
        if (storage.__fake__) {
            storage = storage.data;
        }

        const reg = new RegExp('^' + this.prefix);

        return Object.entries(storage).filter(arr => {
            if (reg.test(arr[0])) {

                arr[1] = this._get(arr[1]);
                if (arr[1] !== defaultValue) {
                    arr[0] = arr[0].replace(reg, '');
                    return true;
                }
            }
            return false;

        });
    }

    /**
     * 
     * @return {object} 
     */
    getAll() {
        const result = {};
        this.getEntries().forEach(([key, value]) => {
            result[key] = value;
        });
        return result;
    }
    _get(value, expireCallback = () => {}) {
        try {
            value = JSON.parse(value);
            const t = Date.now();
            if ('expire' in value && value.expire <= t) {
                // 
                value = defaultValue;
                expireCallback();
            }
            else {
                value = value.data;
            }
        }
        catch (e) {
            value = defaultValue;
        }
        return value;
    }

    /**
     * 
     * @param {string} key  -  
     * @param {any} value  - object
     * @param {number} [expire=0] - 
     * @return {Storage} cache
     */
    set(key, value, expire = 0) {
        expire = (expire || this.expire) | 0;
        const data = {
            data: value
        };
        if (expire !== 0) {
            data.expire = Date.now() + expire;
        }

        key = this.prefix + key;
        this.storage.setItem(key, JSON.stringify(data));
        return this;
    }

    /**
     * 
     * @param  {string} key 
     * @return {any}     null
     */
    get(key) {
        key = this.prefix + key;
        const storage = this.storage;

        return this._get(storage.getItem(key), ()=>{
            storage.removeItem(key);
        });
    }

    /**
     * 
     * @return {Storage} cache
     */
    clear() {
        const keys = this.getKeys();
        const self = this;
        keys.forEach(key => {
            self.remove(key);
        });
        return this;
    }

    /**
     * key
     * @param  {string} key key
     * @return {Storage}     cache
     */
    remove(key) {
        this.storage.removeItem(this.prefix + key);
        return this;
    }
}

/**
 * localstoragesessionStorage
 * @function storage
 * @param  {string} prefix 
 * @param  {number} expire 
 * @param  {string} [type='local']   localsession
 * @return {Storage}        store
 *
 * @example
 * var store = storage('prefix', 60e3, 'session');
 * store.set('a', {abc:1});
 * console.log(store.get('a'));
 * //30s
 * store.set('b',{a:1}, 30E3);
 */
export default function storage(prefix, expire, type) {
    return new Storage(prefix, expire, type);
}
