/**
 * @function type
 * @param {any} obj - 
 * @return {string} array/date/string/object/undefined/function/boolean/null/regexp/math 
 */
const getType = obj =>
    Object.prototype.toString
        .call(obj)
        .slice(8, -1)
        .toLowerCase();

/**
 *  Object.toString 
 * @function isType
 * @param {any} obj - 
 * @param { string} type - 
 * @return {boolean} 
 * @example
 * isType('', 'String') // true
 * isType(new Date, 'Date') // true
 * isType('', 'Array') // false
 */
export function isType(obj, type) {
    return getType(obj) === type.toLowerCase();
}

export default getType;
