/**
 * @file lazy-watch
 * @author leizhihao <leizhihao@baidu.com>
 */
import throttle from 'lodash/throttle';

let instance = null;

/**
 * @class LazyWatch
 * @description (offset)
 * @example
 * LazyWatch.add({ele: document.getElementById('image'), offset: 200})
 */
class LazyWatch {
    constructor(props) {
        this.elements = [];
    }

    /**
     * add
     * @description promise.resolve
     * @param ele {HTMLElement} 
     * @param offset {String|Number} 
     * @return {Promise<never>|Promise<any>}
     */
    add({ele, offset = 0}) {
        // 
        if (!ele) {
            return Promise.reject();
        }
        return new Promise((resolve, reject) => {
            // 
            if (this.elements.some(element => element.ele === ele)) {
                console.warn('LazyWatch: ele is watching !', ele);
                reject();
                return;
            }
            // resolve
            if (this.checkInView(ele, offset)) {
                resolve();
                return;
            }
            this.elements.push({
                ele,
                offset,
                resolve,
            });
        });
    }

    remove(ele) {
        const index = this.elements.indexOf(ele);
        if (index > 0) {
            this.elements.splice(index, 1);
            return true;
        }
        return false;
    }

    /**
     * getViewHeight
     * @description 
     * @return {number}
     */
    getViewHeight() {
        return window.innerHeight || document.documentElement.clientHeight || 0;
    }

    /**
     * checkInView
     * @description 
     * @param element {HTMLElement} 
     * @param offset {String|Number} 
     * @return {boolean}
     */
    checkInView(element, offset) {
        // 
        if (!element) {
            return false;
        }
        const {top, bottom} = element.getBoundingClientRect();
        const viewHeight = this.getViewHeight();
        return top - offset < viewHeight && bottom + offset > 0;
    }

    /**
     * watch
     * @description resolve
     */
    watch() {
        // 
        for (let i = this.elements.length - 1; i >= 0; i--) {
            let {ele, offset, resolve} = this.elements[i];
            if (this.checkInView(ele, offset)) {
                this.elements.splice(i, 1);
                resolve();
            }
        }
    }

    /**
     * initWatch
     * @description 
     */
    initWatch() {
        window.addEventListener('scroll', throttle(this.watch.bind(this), 150));
    }
}

// 
export default (() => {
    // window
    if (typeof window !== 'object') {
        return {};
    }
    if (!instance) {
        instance = new LazyWatch();
        instance.initWatch();
    }
    return instance;
})();
