/**
 * @file  kebab case  camel case
 * @author wangyongqing <wangyongqing01@baidu.com>
 */

/**
 *  kebab case  camel case
 * @function kebab2camel
 * @param {string} source - 
 * @return {string}
 * @example
 * kebab2camel('abc-acd-e-f');
 * // abcAcdEF
 */
export default source => String(source).replace(/-+(.)/gi, (match, alpha) => alpha.toUpperCase());
