eg);\n}\n.w-qes-fireworks .spark {\n  position: absolute;\n  top: 0;\n  width: 6pr;\n  height: 12pr;\n  margin-left: -1px;\n  opacity: 0;\n  will-change: opacity, height;\n  transform-origin: 50% 0;\n}\n.w-qes-fireworks .spark:nth-child(1) {\n  transform: rotate(45deg);\n}\n.w-qes-fireworks .spark:nth-child(2) {\n  transform: rotate(315deg);\n}\n.w-qes-fireworks .spark:nth-child(3) {\n  transform: rotate(135deg);\n}\n.w-qes-fireworks .spark:nth-child(4) {\n  transform: rotate(225deg);\n}\n.w-qes-fireworks .spark:nth-child(6) {\n  transform: rotate(90deg);\n}\n.w-qes-fireworks .spark:nth-child(7) {\n  transform: rotate(180deg);\n}\n.w-qes-fireworks .spark:nth-child(8) {\n  transform: rotate(270deg);\n}\n.w-qes-fireworks .line {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  will-change: transform, height;\n  transform-origin: top center;\n  border-radius: 15pr;\n  fill: rgba(255, 102, 0, 1);\n}\n.nightmode .w-qes-fireworks .line {\n  fill: rgba(128, 51, 0, 1);\n}\n.w-qes-score.animate .active {\n  animation: aniZoom 0.35s 1 ease-in-out;\n}\n.w-qes-score.animate .ipos2 {\n  animation-delay: 0.1s;\n}\n.w-qes-score.animate .ipos3 {\n  animation-delay: 0.2s;\n}\n.w-qes-score.animate .ipos4 {\n  animation-delay: 0.3s;\n}\n.w-qes-score.animate .ipos5 {\n  animation-delay: 0.4s;\n}\n.w-qes-score.animate .spark {\n  animation: aniFade 0.8s linear;\n}\n.w-qes-score.animate .line {\n  animation: aniBoom 0.8s ease-in-out;\n}\n.w-qes-score.animate .fire2 .spark,\n.w-qes-score.animate .fire2 .line {\n  animation-delay: 0.2s;\n}\n.w-qes-score.animate .fire3 .spark,\n.w-qes-score.animate .fire4 .spark,\n.w-qes-score.animate .fire3 .line,\n.w-qes-score.animate .fire4 .line {\n  animation-delay: 0.3s;\n}\n.w-qes-score.animate .fire5 .spark,\n.w-qes-score.animate .fire5 .line {\n  animation-delay: 0.4s;\n}\n.w-qes-score.animate .fire6 .spark,\n.w-qes-score.animate .fire6 .line {\n  animation-delay: 0.5s;\n}\n.w-qes-score.animate .fire7 .spark,\n.w-qes-score.animate .fire7 