/**
 *  html 
 * @function escapeHtml
 * @param {string} source - 
 * @return {string} 
 * @example
 * escapeHtml('<a href=""></a>')
 * //&lt;a href=&quot;&quot;&gt;&lt;/a&gt;
 */
export default source => {
    return String(source)
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
        .replace(/`/g, '&#x60;')
        .replace(/'/g, '&#39;');
};
