/**
 * @file url query json
 * @author wangyongqing <wangyongqing01@baidu.com>
 */
import decode from './decode-uri-component';
/**
 * url query json
 * @function query2json
 * @param {string} url  - url
 * @param {boolean} [safe = true]-  decode  true
 * @return {Object} obj - json
 */
export default (url, safe = true) => {
    const locse = url.split('?');

    const search = locse[1] ? locse[1] : locse[0];
    const pairs = search.split('&');
    const result = {};
    pairs.forEach(pair => {
        pair = pair.split('=');
        if (pair[0].length > 0) {
            let resultPair = '';
            try {
                resultPair = (safe ? decode(pair[1]) : decodeURIComponent(pair[1])) || '';
            } catch (e) {
                console.log(e);
            }
            result[pair[0]] = resultPair;
        }
    });
    return result;
};
