import {isType} from './type';

/**
 *  null  undefined
 * @param {any} obj - 
 * @return {boolean} 
 * @example
 * isNil(null) // true
 * isNil(undefind) // true
 * isNil('') // false
 */
export function isNil(obj) {
    return obj === undefined || obj === null;
}
/**
 *  undefined
 * @param {any} obj - 
 * @return {boolean} 
 * @example
 * isUndefined(null) // false
 * isUndefined(undefind) // true
 * isUndefined('') // false
 */
export function isUndefined(obj) {
    return obj === undefined;
}
/**
 *  window 
 * @param {any} obj - 
 * @return {boolean} 
 * @example
 * isWindow( window) // true
 * isWindow(self) // true
 * isWindow('') // false
 */
export function isWindow(obj) {
    return !isNil(obj) && obj === obj.window;
}
/**
 * 
 * @param {any} obj - 
 * @return {boolean} 
 * @example
 * isPlainObject({}) // true
 * isPlainObject(window) // false
 */
export function isPlainObject(obj) {
    if (isType(obj, 'Object')) {
        return Object.getPrototypeOf(obj) === null || Object === obj.constructor;
    }

    return false;
}

/**
 * function
 * @name isFunction
 * @function isFunction
 * @param  {any} obj 
 * @return {Boolean} bool  true|false
 * @example
 * isFunction(new Function()) // true
 * isFuction('') // false
 */
export function isFunction(obj) {
    return isType(obj, 'Function');
}

/**
 * string
 * @name isString
 * @function
 * @param  {any} obj 
 * @return {Boolean} bool true|false
 * isString([]) // false
 * isString('array') // true
 */
export function isString(obj) {
    return isType(obj, 'String');
}

/**
 * array
 * @param  {any} obj 
 * @return {Boolean} bool true|false
 * @example
 * isArray([]) // true
 * isArray('array') // false
 */
export function isArray(obj) {
    return isType(obj, 'Array');
}

/**
 * Number
 * @param  {any} obj 
 * @return {Boolean} bool  true|false
 * @example
 * isNumber(1) // true
 * isNumber('1') // false
 */
export function isNumber(obj) {
    return isType(obj, 'Number');
}

/**
 * 
 * @param {any} bool - 
 * @return {Boolean} bool  true|false
 * @example
 * isBoolean(false) // true
 * isBoolean(/a/) // false
 */
export function isBoolean(bool) {
    return bool === true || bool === false;
}

/**
 * 
 * @param {string} subject - 
 * @return {boolean} 
 * @example
 * isEmpty(''); // => true
 * isEmpty('  '); // => false
 * isEmpty('World'); // => false
 */
export function isEmpty(subject) {
    return String(subject).length === 0;
}

/**
 * 
 * @param {string} subject - 
 * @return {boolean} 
 * @example
 * isBlank('');// => true
 * isBlank('  ');// => true
 * isBlank('World');// => false
 */
export function isBlank(subject) {
    return String(subject).trim().length === 0;
}
/**
 * 
 * @param {object} obj - 
 * @return {boolean} 
 * @example
 * isEmptyObject({}) // true
 */
export function isEmptyObject(obj) {
    return Object.keys(obj).length === 0 && obj.constructor === Object;
}

const reJSON = /^\{[\s\S]*\}$|^\[[\s\S]*\]$/;

export function isJSON(value) {
    if (typeof value !== 'string') {
        return false;
    }
    if (!reJSON.test(value)) {
        return false;
    }
    try {
        JSON.parse(value);
    } catch (err) {
        return false;
    }
    return true;
}
