/**
 *  stringify
 * @file  stringify
 * @function stringify
 * @param {object} json -  stringify 
 * @param {*} encode -  encodeURIComponent 
 */
export default (json, encode = false) => {
    if (typeof json === 'object') {
        json = JSON.stringify(json);
    }
    if (encode) {
        json = encodeURIComponent(json);
    }
    return json;
};
