import query2json from './query2json';
/**
 *  url url  query
 * @function parseUrl
 * @param {string} input - 
 * @param {boolean} [parseQueryString = false] -  query  object
 * @return {object} {url,query}
 * @example
 * parseUrl('http://baidu.com?abc=1', true);
 *   {
 *       url: 'http://baidu.com',
 *       query: {
 *           abc: 1
 *       }
 *   }
 *   parseUrl('http://baidu.com?abc=1')
 *   {
 *       url: 'http://baidu.com',
 *       query: 'abc=1'
 *   }
 */
export default (input, parseQueryString = false) => {
    const [url, query = ''] = input.split('?');
    return {
        url,
        query: parseQueryString ? query2json(query) : query
    };
};

