/**
 * @file app
 * @link http://agroup.baidu.com/share/md/0d6d1cc6b1c7417e8071057fb436001b
 * @author wangyongqing <wangyongqing01@baidu.com>
 */
import UA from './ua';
/**
 * ua
 * @function isMatrix
 * @param {''|'youjia'} [subName=''] - 
 * @param {string} ua -  userAgent navigator.userAgent
 * @return { boolean} 
 * @example
 * isMatrix()
 * isMatrix('youjia')
 * isMatrix('youjia', 'fdafafdafaf pro baiduboxapp')
 */
export default (subName = '', ua = UA) => {
    if (subName === 'main') {
        subName = '';
    }
    const matrixUaExp = /\bbdapp\/\d+\.\d+\s+\((\w+);\s*(\w+)\)/;
    /**
     * Android	Mozilla/5.0 (.83 Mobile Safari/537.36 T7/11.16 light/1.0 bdapp/1.0 (youjia; youjia) youjia/1.0.1 (Baidu; P1 8.0.0)
     *  na Dalvik/2.1.0 (Linux; U; Android 8.0.0; MHA-AL00 Build/HUAWEIMHA-AL00) okhttp/3.11.0 bdapp/1.0 (youjia; youjia) youjia/1.0.1 (Baidu; P1 8.0.0)
        iOS	Mozilla/5.0 (iPhone; C.1.15 (KHTML, like Gecko) Mobile/15E148 main/1.0 SP-engine/2.14.0 NABar/1.0 bdapp/1.0 (youjia; youjia) youjia/1.0.1 (Baidu; P2 13.3)
        naMozilla/5.0 (iPho5.1.15 (KHTML, like Gecko) Mobile/15E148 youjia/1.0.1 (Baidu; P2 13.3)
     */
    if (subName === '') {
        let m = /\s(lite|pro|info|mission)\sbaiduboxapp\//.exec(ua);
        if (m) {
            return true;
        }
        // bdapp/1.0
        if (/\bbdapp\/\d+\.\d+\s/.test(ua)) {
            return true;
        }
    } else if (subName === false) {
        // matrixyoujialite info
        let m = ua.match(matrixUaExp);
        if (m && m[1]) {
            return true;
        }
    } else {
        // TODO 
        if (new RegExp(new RegExp(`\\s${subName}\\sbaiduboxapp\/`)).test(ua)) {
            return true;
        }
        let m = ua.match(matrixUaExp);
        if (m && m[1] && m[1] === subName) {
            return true;
        }
    }

    return false;
};
