/**
 * 
 * @function typeError
 * @param {string} name - 
 * @param {string} wanted - 
 * @param {any} obj - 
 * @return {TypeError} 
 */
export default (name, wanted, obj) => new TypeError('Expected `' + name
    + '` to be of type `' + wanted
    + '`, got `' + typeof obj + '`');

